<template>
  <div class="container">
    <page-title first-title="分销数据看板"></page-title>
    <section class="search-wrapper">
      <el-row :gutter="20">
        <el-col :span="6">
          <div class="grid-content bg-purple">
            用户账号：
            <span>{{ this.wor_id }}</span>
          </div>
        </el-col>
        <el-col :span="6" :offset="6">
          <div class="grid-content bg-purple">
            小店名称：
            <span>{{ this.$route.params.name }}</span>
          </div>
        </el-col>
      </el-row>
    </section>
    <div class="search-wrapper">
      <el-form :inline="true">
        <el-form-item label="商品货号">
          <el-input v-model="params.spu" placeholder="商品货号"></el-input>
        </el-form-item>
        <el-form-item label="商家">
          <el-autocomplete
            class="inline-input"
            :fetch-suggestions="querySearch"
            placeholder="请输入内容"
            v-model="params.suppliersName"
            clearable
          ></el-autocomplete>
        </el-form-item>
        <el-form-item label="商品名称">
          <!-- UserId -->
          <el-input v-model="params.goodsName" placeholder="商品名称"></el-input>
        </el-form-item>
        <!-- UserId -->
        <el-form-item label="状态">
          <el-select v-model="params.status" placeholder="状态" @change="statusChage">
            <el-option
              v-for="status in StatusOption"
              :key="status.id"
              :label="status.name"
              :value="status.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search" icon="el-icon-search">搜索</el-button>
          <el-button type="primary" @click="clear" icon="el-icon-refresh-left">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table :data="retailsupplier_list" stripe :header-cell-style="{ background: '#eef1f6' }">
        <el-table-column prop="spu" label="SPU货号"></el-table-column>
        <el-table-column label="商品图">
          <template slot-scope="scope">
            <img v-show="scope.row.goodsPic" :src="scope.row.goodsPic" width="50" height="50" />
          </template>
        </el-table-column>
        <el-table-column prop="goodsName" label="商品名称"></el-table-column>
        <el-table-column label="本店售价/积分价">
          <template slot-scope="scope">
            <span>￥{{ scope.row.ourShopPrice }}</span>
            <span>Ⓗ {{ scope.row.ourShopCoin }}</span>
          </template>
        </el-table-column>
        <el-table-column label="分销底价/积分价">
          <template slot-scope="scope">
            <span>￥{{ scope.row.realShopPrice }}</span>
            <span>Ⓗ {{ scope.row.distributionCoin }}</span>
          </template>
        </el-table-column>
        <el-table-column label="+￥+Ⓗ">
          <template slot-scope="scope">
            <span>￥{{ scope.row.differencePrice }}</span>
            <span>Ⓗ {{ scope.row.differenceCoin }}</span>
          </template>
        </el-table-column>
        <el-table-column label="运营底价/积分价">
          <template slot-scope="scope">
            <span>￥{{ scope.row.shopPrice }}</span>
            <span>Ⓗ {{ scope.row.operationCoin }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="actName" label="活动名称"></el-table-column>
        <el-table-column prop="startTime" label="开始时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="endTime" label="结束时间" :formatter="dateFormat"></el-table-column>
        <el-table-column label="活动运营底价/积分价">
          <template slot-scope="scope">
            <span>￥{{ scope.row.actOperationPrice }}</span>
            <span>Ⓗ {{ scope.row.actOperationCoin }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="newcomerCommission" label="拉新奖励" width="50"></el-table-column>
        <el-table-column prop="activityCommission" label="活动奖励" width="50"></el-table-column>
        <el-table-column prop="mlmShopPrice" label="分销价"></el-table-column>
        <el-table-column prop="volume" label="成交数"></el-table-column>
        <el-table-column prop="conversionRate" label="转化率成交/点击"></el-table-column>
        <!-- <el-table-column prop="activityCommission" label="状态" :formatter="statusName"></el-table-column> -->
        <el-table-column prop="statusName" label="状态"></el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="params.pageSize"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
export default {
  name: 'DistributionDatil',
  data() {
    return {
      wor_id: this.$route.params.id ? this.$route.params.id : '',
      retailsupplier_list: [],
      currentPage: 1,
      StatusOption: [{ id: 1, name: '上架中' }, { id: 0, name: '已下架' }],
      params: {
        pageNumber: 1,
        pageSize: 10,
        spu: '',
        suppliersName: '',
        goodsName: '',
        status: '',
        mobilePhone: '',
        userName: ''
      },
      total: 0,
      restaurants: []
    }
  },
  created() {
    this.getList()
  },
  mounted() {
    this.restaurants = this.getSer()
  },
  methods: {
    getList() {
      let pramstr = JSON.stringify(this.params)
        .replace(/:/g, '=')
        .replace('{', '')
        .replace('}', '')
        .replace(/,/g, '&')
        .replace(/"/g, '')
        .replace(/\s+/g, '')
      console.log(this.wor_id)
      this.$api.get('/mlm/data-board/users/' + this.wor_id + '/goods/list?' + pramstr, null, res => {
        if (res.code === 1) {
          this.retailsupplier_list = res.data.mlmShopGoodsList
          this.total = res.data.totalElements
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.params.pageNumber = currentPage
      this.getList()
    },
    getSer() {
      this.$api.get('/wo/merchant/userType', null, r => {
        if (r.code == 1) {
          var arrName = []
          r.data.suppliersList.forEach(function(value) {
            let item = {}
            item['value'] = value.suppliersName
            arrName.push(item)
          })
          this.restaurants = arrName
        } else {
          this.$message.error(r.msg)
        }
      })
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants
      cb(results)
    },
    createFilter(queryString) {
      return restaurant => {
        return restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
      }
    },
    statusChage(sta) {
      this.params.status = sta
    },
    search() {
      this.params.pageNumber = 1
      this.getList()
    },
    clear() {
      this.getList()
      this.params.pageNumber = 1
      this.params.pageSize = 10
      this.params.spu = ''
      this.params.suppliersName = ''
      this.params.goodsName = ''
      this.params.status = ''
    },
    statusName: function(row, column) {
      let data = row[column.property]
      return data == 1 ? '上架中' : '已下架'
    },
    dateFormat: function(row, column) {
      let date = row[column.property]
      if (date) {
        return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3)
      } else if (date == 0) {
        return '长期有效'
      }
    }
  }
}
</script>
<style lang="scss">
.el-pagination {
  margin-top: 10px;
  text-align: center;
}
.list-wrapper span {
  display: block;
}
</style>
