<template>
  <div class="container">
    <page-title first-title="分销数据看板"></page-title>
    <div class="search-wrapper">
      <el-form :inline="true">
        <el-form-item label="小店名称">
          <el-input v-model="params.userName" placeholder="小店名称"></el-input>
        </el-form-item>
        <el-form-item label="用户编号">
          <el-input v-model="params.userId" placeholder="用户编号"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search" icon="el-icon-search">搜索</el-button>
          <el-button type="primary" @click="clear" icon="el-icon-refresh-left">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table :data="RetailSupplierList" :header-cell-style="{ background: '#eef1f6' }" style="width: 100%">
        <!-- supplierID -->
        <el-table-column prop="userId" label="用户账号"></el-table-column>
        <!-- SKU_Item -->
        <el-table-column prop="userName" label="小店名称"></el-table-column>
        <!-- supplierName -->
        <el-table-column prop="userMoney" label="账户佣金"></el-table-column>
        <el-table-column prop="totalMoney" label="佣金总额"></el-table-column>
        <!-- basePrice_setTime -->
        <el-table-column prop="amount" label="累积提现" :formatter="numb"></el-table-column>
        <!-- sellPrice -->
        <el-table-column prop="payPeopleNum" label="付款人数"></el-table-column>
        <!-- retailBasePrice -->
        <el-table-column prop="payNum" label="付款笔数"></el-table-column>
        <!-- D_value -->
        <el-table-column prop="orderAmount" label="销量总额"></el-table-column>
        <!-- operateBasePrice -->
        <el-table-column prop="inviteCount" label="粉丝数"></el-table-column>
        <!-- activityName -->
        <el-table-column prop="total" label="小店点击" :formatter="numb"></el-table-column>
        <!-- startTime -->
        <el-table-column prop="shelfCount" label="累计上架商品" :formatter="numb"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <!-- supplierID -->
            <el-button @click="ViewDatil(scope.row)" type="text" size="small">查看分销<br />商品列表</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="params.pageSize"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: 'Distributiondata',
  data() {
    return {
      RetailSupplierList: [],
      currentPage: 1,
      params: {
        pageNumber: 1,
        pageSize: 10,
        userName: '',
        userId: ''
      },
      total: 0
    }
  },
  created() {
    this.getList()
  },
  methods: {
    search() {
      this.params.pageNumber = 1
      this.getList()
    },
    getList() {
      let pramstr = JSON.stringify(this.params)
        .replace(/:/g, '=')
        .replace('{', '')
        .replace('}', '')
        .replace(/,/g, '&')
        .replace(/"/g, '')
        .replace(/\s+/g, '')
      this.$api.get('/mlm/data-board/users/list?' + pramstr, null, res => {
        if (res.code === 1) {
          this.RetailSupplierList = res.data.dataBoardList
          this.total = res.data.totalElements
        } else {
          this.RetailSupplierList = []
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.params.pageNumber = currentPage
      this.getList()
    },
    clear() {
      this.params.pageNumber = 1
      this.params.pageSize = 10
      this.params.userName = ''
      this.params.userId = ''
      this.getList()
    },
    ViewDatil(row) {
      // this.$router.push({ name: 'DistributionDatil', params:{ id: id} })
      this.$router.push({
        name: 'DistributionDatil',
        params: { id: row.userId, name: row.userName, number: row.mobilePhone }
      })
    },
    numb: function(row, column) {
      let data = row[column.property]
      return data ? data : '0'
    }
  }
}
</script>

<style lang="scss">
.header {
  margin-bottom: 10px;
}
.container {
  .search-wrapper {
    padding: 10px;
    margin-bottom: 10px;
    background: #fff;
    border: 1px solid #eee;
    .el-form-item {
      margin-bottom: 10px;
    }
  }
  .list-wrapper {
    margin-top: 10px;
    .nums {
      width: 40px;
    }
  }
}
.el-pagination {
  margin-top: 10px;
  text-align: center;
}
</style>
